/******* CUSTOMER REVIEW ELEMENTS *******/

@media (min-width: 768px) {
  .customerBlock .container {
    padding: 0;
  }
}

.customerReview {
  margin-bottom : 3rem;
  border        : 1px solid rgba(155, 155, 155, 0.3);
  border-radius : 6px;
  background    : white;
  color         : var(--textColor);
  text-align    : center;
  padding       : 0 3rem;
}

.customerReview__photo {
  margin-top: -1.5rem;
  height: 9rem;
  width: auto;
}

