  color: var(--accentColor) ;
}

.customerReview__more {
  text-align: center;
  color: #2C99ED;
  font-weight: 500;
}






/******    /CONTACTS   *******/

@media (min-width: 992px) {
  .contactBlock {
    padding-left: 1.5rem;
  }
}

.contactItem {
               /*  border-top: 1px solid #E1E7EB;
                 padding: 1rem 0;*/
  margin-bottom: 1.5rem;

  @nest &--last {
    border-bottom: 1px solid #E1E7EB;
